import React from 'react';
import './App.css';
import ListItem from './ListItem';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faTrash } from '@fortawesome/free-solid-svg-icons';

library.add(faTrash);  /* WHAT IS THIS.#@#$#$%#$^#$@#%^^%&%^*^^&*%$%@#$#$^%^*^&*$$SLFKSNAEJ;VNEIO6546645S*/

class App extends React.Component {
  
  constructor(props){
    super(props);
    this.state = {
      items: [],
      currentItem: {
        text: '',
        key: ''
      }
    }
    this.handleInput = this.handleInput.bind(this);
    this.addInput = this.addInput.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.setUpdate = this.setUpdate.bind(this);   
  }

  handleInput(e){
    this.setState({
      currentItem:{
        text: e.target.value,
        key: Date.now()
      }
    })
  }

  addInput(e){
    e.preventDefault()
    const newItem = this.state.currentItem
    console.log(newItem)
    if(newItem!==""){
      const totalItem = [...this.state.items, newItem];
      this.setState({
        items: totalItem,
        currentItem: {
          text: '',
          key: ''
        }
      })
    }
  }

  deleteItem(key){
    const filteredItems= this.state.items.filter(item => item.key !==key);
    this.setState({
      items: filteredItems
    })
  }

  setUpdate(text, key){
    const items = this.state.items;
    items.map(item => {
      if(item.key===key){
        item.text=text;
      }
    })
    this.setState({
      items:items
    })
  }
  
  render() {
    return (
      <div className='App'>
        <form id='todoform' onSubmit={this.addInput}>
          <input 
            type='text' 
            placeholder='Enter task' 
            value={this.state.currentItem.text} 
            onChange={this.handleInput}>
          </input>
          <button type='submit'>Add</button>
        </form>
        <ListItem item={this.state.items}  deleteItem={this.deleteItem} setUpdate={this.setUpdate}/>
      </div>
    )
  }
};

export default App;
